import React, { useState } from 'react';
import useStats from './useStats';

export default function CountryStat() {
  const [seleceted, setSeleceted] = useState();
  const { loading, error, stats } = useStats(
    'https://covid19.mathdro.id/api/countries'
  );
  console.log(seleceted);
  const handleChange = e => {
    console.log(e.target.value);
  };
  return (
    <div>
      <select onChange={handleChange} seleceted="NG">
        {stats?.countries &&
          Object.entries(stats.countries).map((country, i) => (
            <option value={country[1]} key={i}>
              {country[0]}
            </option>
          ))}
      </select>
    </div>
  );
}
